.header-main {
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-left: 10%;
    padding-right: 60px;
    padding-top: 50px;
    z-index: 9;

    &__menu {
        font-size: 14px;
        color: #2196b4;
        text-transform: uppercase;
        display: flex;
        &::before{
            content: '';
            width: 20px;
            height: 19px;
            position: absolute;
            top: 3px;
            left: 75px;
            cursor: pointer;
        }
    }

    &__menu.white {
        color: white;
    }

    &__menu-wrapper {
        position: relative;
    }

    &__right {
        display: flex;
        justify-content: center;
        // align-items: center;
    }

    &__button-wrapper {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__elements {
        display: flex;
        flex-direction: column;
        width: 40px;
        margin-left: 15px;
        white-space: nowrap;
        cursor: pointer;

        span+span {
            margin-top: 4px;
        }

        span {
            display: inline-block;
            height: 1px;
            background-color: #2196b4;
            transition: all .3s ease;

            &:nth-of-type(1) {
                width: 100%;
            }

            &:nth-of-type(2) {
                width: 85%;
            }

            &:nth-of-type(3) {
                width: 70%;
            }
        }
    }

    &__elements.active {

        span {
            &:nth-of-type(3) {
                width: 100%;
            }

            &:nth-of-type(2) {
                width: 85%;
            }

            &:nth-of-type(1) {
                width: 70%;
            }
        }

    }

    &__elements.white {
        span {
            background-color: #fff;
        }
    }

    &__contacts {
        display: flex;
        flex-direction: column;
        justify-content: center;
        a {
            text-align: right;
            color: #2196b4;
            text-transform: uppercase;
        }
    }

    &__contacts.white {
        a {
            color: #ffffff;
        }
    }

    &__btn {
        background-color: #2196b4;
        font-size: 20px;
        padding: 10px 20px;
        cursor: pointer;
        text-transform: uppercase;
        margin-left: 40px;
        transition: all .3s ease;

        &:hover {
            background-color: #1f859e;
        }
    }
}

// dropdown menu

.dropdown {
    position: absolute;
    display: flex;
    color: #2196b4;
    font-size: 14px;
    text-transform: uppercase;
    flex-direction: column;
    left: 62px;
    top: 35px;


    a+a {
        margin-top: 15px;
    }

    transition: all .2s ease;

    a {
        transition: all .1s ease;
        &:hover {
            color: #1f859e;
        }
    }

    &__element {
        white-space: nowrap;
    }
}

.dropdown.active {
    a {
        opacity: 0;
        pointer-events: none;
        transform: translate(0, -15px);
    }
}

.dropdown.white {
    a {
        color: white;
    }
}

@media screen and (max-width: 1300px) {}

/*HEADER*/

.header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999;
    padding: 40px 2% 40px 35%;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header.no-bg {
    background: none;
}

.header-title {
    text-transform: uppercase;
    font-size: 20px;
    white-space: nowrap;
}

.feedback {
    text-align: right;
    display: flex;
    align-items: center;
    white-space: nowrap;
}

.feedback-order {
    text-transform: uppercase;
    color: #fff;
    background: #1d345c;
    padding: 5px 15px;
    display: inline-block;
    font-size: 17px;
}

.feedback-contacts {
    display: inline-block;
    line-height: 1;
    margin-right: 40px;
}

.feedback-contacts a {
    color: #1d345c;
    text-decoration: none;
    font-size: 15px;
}

.header.no-bg .feedback-order {
    color: #1d345c;
    background: #fff;
}

.header.no-bg .feedback-contacts a {
    color: #fff;
}

/*SIDEBAR*/

.sidebar {
    max-width: 310px;
    min-width: 100px;
    width: 15%;
    height: 100%;
    position: fixed;
    background-color: #179eaf;
    background-image: url(../assets/images/background-full.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 9999;
}

/*PAGE*/

.page {
    padding: 200px 15% 200px 35%;
    position: relative;
    z-index: 1;
}

/*SLIDER PAGE*/

.slider-page {
    background: #179eaf;
    width: 100%;
    margin-bottom: 45px;
}

.slider-page img {
    width: 100%;
}

.footer {
    position: fixed;
    bottom: 0;
    right: 0;
    width: 15%;
    z-index: 0;
}

.footer img {
    width: 100%;
}

/*SLIDER HOME*/

.logo-box {
    width: 30%;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap;
}

.logo {
    width: 50%;
    margin: 0 auto;
}

.slider-home {
    background-color: #000;
    // background-image: url(../assets/images/bg_3.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 100%;
}

//header index

.header.header--index {
    background-color: transparent;
}

.title-architecture {
    text-transform: uppercase;
}

// .header-main__elements.active {
//     background-color: #fff;
// }

.header--absolute {
    position: absolute;
    left: 17%;
    top: 45px;
    
}

.header-main__menu--color, 
.dropdown--color,
.header-main__elements--color {
    color: #1d345c;
    span {
        background-color: #1d345c;
    }
}