/*HEADER*/

.header {
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 999;
	padding: 40px 2% 40px 35%;
	background: #fff;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.header.no-bg {
	background: none;
}

.header-title {
	text-transform: uppercase;
	font-size: 20px;
	white-space: nowrap;
}

.feedback {
	text-align: right;
	display: flex;
	align-items: center;
	white-space: nowrap;
}

.feedback-order {
	text-transform: uppercase;
	color: #fff;
	background: #1d345c;
	padding: 5px 15px;
	display: inline-block;
	font-size: 17px;
}

.feedback-contacts {
	display: inline-block;
	line-height: 1;
	margin-right: 40px;
}

.feedback-contacts a{
	color: #1d345c;
	text-decoration: none;
	font-size: 15px;
}

.header.no-bg .feedback-order{
	color: #1d345c;
	background: #fff;
}
 
.header.no-bg .feedback-contacts a{
	color: #fff;
}

/*SIDEBAR*/

.sidebar {
	max-width: 310px;
	min-width: 100px;
	width: 15%;
	height: 100%;
	position: fixed;
	background-color: #179eaf;
	// background-image: url(../assets/images/bg-full.jpg);
	background-size: cover;
	background-repeat: no-repeat;
	z-index: 9999;
}

/*PAGE*/

.page {
	padding: 200px 15% 200px 35%;
	position: relative;
	z-index: 1;
}

/*SLIDER PAGE*/

.slider-page {
	background: #179eaf;
	width: 100%;
	margin-bottom: 45px;
}

.slider-page img {
	width: 100%;
}

.footer {
	position: fixed;
	bottom: 0;
	right: 0;
	width: 15%;
	z-index: 0;
}

.footer img {
	width: 100%;
}

/*SLIDER HOME*/

.logo-box {
	width: 30%;
	color:#fff;
	text-align: center;
	text-transform: uppercase;
	white-space: nowrap;
}

.logo {
	width: 50%;
	margin: 0 auto;
}

.slider-home{
	background-color: #000;
	// background-image: url(../assets/images/bg_3.jpg);
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	width: 100%;
	height: 100%;
}