.page-contacts {
    padding-right: 2%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.page {
    &__left {
        display: flex;
        flex-direction: column;
        // margin-right: 130px;
        h1  {
            margin: 0 0 120px;
            font-size: 54px;
            text-transform: uppercase;
            max-width: 265px;
        }
    }

    &__container {
        display: flex;
        flex-direction: column;

        span, p {
            color: #231f20;
            font-size: 18px;
        }

        span {
            font-family: 'ttdrugs-bold';
        }

        span + span {
            margin-bottom: 45px;
        }

        p {
            margin: 0 0 60px;
            max-width: 525px;
        }
    }

    &__btn {
        text-transform: uppercase;
        color: #fff;
        background: #1d345c;
        padding: 5px 15px;
        display: inline-block;
        font-size: 17px;
        max-width: 250px;
    }

    &__image-container {
        width: 30vw;
    }

    &__map {
        margin-top: 170px;
        width: 100%;
        height: 700px;
    }
}

.map {
    width: 500px;
    height: 500px;
}
