/*SLIDER PAGE*/

.wrapper {
    &--slider {
        background-image: none;

        &:before,
        &:after {
            content: none;
        }
    }
}

.slider-page {
    background: #179eaf;
    width: 100%;
    margin-bottom: 45px;
}

.slider-page img {
    width: 100%;
}

.footer {
    position: fixed;
    bottom: 0;
    right: 0;
    width: 15%;
    z-index: 0;
}

.footer img {
    width: 100%;
}

/*SLIDER HOME*/

.logo-box {
    width: 30%;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap;
}

.logo {
    width: 50%;
    margin: 0 auto;
}

.slider-home {
    flex-direction: column;
    // background-color: #000;
    // background-image: url(../assets/images/bg_3.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 100%;

    &__logo-wrapper {
        display: flex;
        justify-content: center;
        align-content: center;
        align-items: center;
        z-index: 8;
    }

    &__descriptions {
        p {
            font-size: 24px;
            color: white;
            text-transform: uppercase;
        }

    }
}

.slider {
    position: absolute;
    width: 100%;
    height: 100%;

    &__wrapper {}

    &__picture {
        background-color: #000;
        background-image: url(../assets/images/bg_3.jpg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        width: 100%;
        height: 100%;
    }
}

.swiper-container {}